@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Signika:wght@500&display=swap');


/* -------------------Navbar------------------ */
.logo1 {
  width: 12%;
}


/* -------------------Registration Page----------------- */
.signup {
  width: 800px;
  height: 540px;
  margin-bottom: 50px;
  box-shadow: 0px 0px 10px 2px #d7dae5;
  border-radius: 5px;
}



.form-group {
  width: 240px;
  margin-left: 35px;
  float: left;
}

.form-title {
  padding-top: 20px;
  text-align: center;
  font-size: 2rem;
  font-family: initial;
}

.inp {
  border: none;
  border-bottom: 2px solid #000000;
  padding: 4px 10px;
  margin: 25px 0px 0px 20px;
  outline: none;
  width: 85%;
}

.inp-ph::-webkit-outer-spin-button,
.inp::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

[placeholder]:focus::-webkit-input-placeholder {
  transition: text-indent 0.2s 0.2s ease;
  text-indent: -100%;
  opacity: 1;
}

.signup-img {
  margin: auto;
}

.register-pwd-eye {
  margin-left: -18px;
}

/* ------------------------------Login page------------------ */
.login {
  width: 500px;
  height: 340px;
  margin: auto;
  margin-bottom: 50px;
  background-image: url("../src/photos/loginImg.jpg");
  background-position: center;
  background-size: 95%;
  background-repeat: no-repeat;
  box-shadow: 0px 0px 10px 2px #d7dae5;

  border-radius: 5px;
}

.login-title {
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.login-form {
  width: 320px;
  margin: auto;
}

.pwd-eye {
  margin-left: -30px;
}

.login-inp {
  border: none;
  color: white;
  font-weight: 500;
  border-bottom: 2px solid #000000;
  padding: 4px 10px;
  margin: 25px 0px 0px 20px;
  outline: none;
  width: 85%;
  background-color: #21212230;
}

/* ------------------------------Home Page----------------------- */
.home {
  height: 619px;
  background: linear-gradient(90deg, #9cbad6 50%, #9ca3af 50%);
}

.home-heading {
  text-align: center;
  margin: auto;
}


/* ---------------------------Contact Page---------------------------- */
.contact_info {}

.contact_info_item {
  background-color: #ffffffc9;
  width: 320px;
  padding: 8px 20px;
  box-shadow: 0px 0px 10px 2px #d7dae5;
  border-radius: 5px;

}

.contact_info_content {
  margin: 5px 20px;

}

.contact_info_title {
  font-size: 18px;
  font-family: 'Roboto Mono', monospace;
}

.contact_info_text {
  font-size: 15px;
  /* opacity: 0.7; */
  color: #606060;
}

.contact_form {
  width: 780px;
  border-radius: 7px;
  padding: 55px;
  margin: auto;
  box-shadow: 0px 0px 10px 2px #d7dae5;
}

.contact_inp {
  outline: none;
  padding: 7px 15px;
  width: 200px;
  border-radius: 3px;
  border: 1px;
  box-shadow: 0px 0px 10px 2px #d7dae5;
}


/* -----------------------------------About Page-------------------------- */
.emp_profile {
  width: 800px;
  padding: 20px;
  border-radius: 5px;
  background: rgb(255, 255, 255);
  box-shadow: 0px 0px 10px 2px #d7dae5;
}

.emp_img {
  border: 1px solid black;
  width: 150px;
}

.profile_head h5 {
  font-size: 35px;
  font-family: 'Signika', sans-serif;
}

.profile_head p,
h6 {
  color: #606060;
}

.profile_edit_btn {
  border: none;
  background-color: #ffffff;
}

.profile_work {
  margin: 0px 0px 0px 25px;
}

.profile_work p {
  font-size: 20px;
  margin: 0;
}

.profile_work a {
  text-decoration: none;

}

.tab_content {
  margin-left: -45px;
  margin-top: 25px;
}

/* ---------------------------------Error 404 page note found------------------------- */
.error_page {
  text-align: center;
  position: relative;
}

.error_404 {
  font-size: 15rem;
  z-index: -1;
  color: #00000029;
}

.error_warning {
  font-weight: 800;
  z-index: 1;
  margin-top: -12rem;
  font-size: 3rem;
  color: rgb(0 48 183);
}

.error_warning a {
  font-size: 2rem;
}